import React from 'react';
import injectSheet from 'react-jss';
import PageTitle from './../shared/page-title';

import image1 from './../img/artwork/cry_havok.png';
import image2 from './../img/artwork/earth.png';
import image3 from './../img/artwork/globus.png';
import image4 from './../img/artwork/bolts.png';
import image5 from './../img/artwork/green_light.png';
import image6 from './../img/artwork/desk.jpg';
import image7 from './../img/artwork/spheres.jpg';
import image8 from './../img/artwork/attic.jpg';
import image9 from './../img/artwork/bedroom.jpg';
import image10 from './../img/artwork/warhead.jpg';
import image11 from './../img/artwork/freeman.jpg';
import image12 from './../img/artwork/fan.jpg';
import image13 from './../img/artwork/tank.jpg';

const styles = {
    root: {
        '& h2': {
            'margin-bottom': '30px'
        }
    },

    item: {
        'margin-bottom': '40px'
    },

    itemImage: {
        width: '100%',
        height: 'auto'
    },

    itemDesc: {
        flex: '1',

        '& h3': {
            color: 'red'
        }
    }
}

const Item = injectSheet(styles)(({ image, description, classes }) => {
    return (
        <div className={classes.item}>
            <div>
                <img className={classes.itemImage} src={image}/>
            </div>
            <div className={classes.itemDesc}>
                {description}
            </div>
        </div>
    );
})

class Page extends React.Component {
    render() {
        const { classes } = this.props;

        return (
            <div className={classes.root}>
                <PageTitle text="Old Artwork"/>

                <h2>2011</h2>
                <Item image={image1} description="Made with: Blender, GIMP, Inkscape"/>
                <Item image={image2} description="Made with: Blender"/>
                <Item image={image3} description="Made with: Blender"/>
                <Item image={image4} description="Made with: Blender (Cycles)"/>

                <h2>2010</h2>
                <Item image={image5} description="Made with: Blender"/>

                <h2>2009</h2>
                <Item image={image6} description="Made with: 3ds Max"/>

                <h2>2006</h2>
                <Item image={image7} description="Made with: 3ds Max"/>
                <Item image={image8} description="Made with: 3ds Max"/>
                <Item image={image9} description="Made with: 3ds Max"/>
                <Item image={image10} description="Made with: 3ds Max"/>
                <Item image={image11} description="Made with: 3ds Max"/>

                <h2>2005</h2>
                <Item image={image12} description="Made with: 3ds Max"/>
                <Item image={image13} description="Made with: 3ds Max"/>
            </div>
        );
    }
}

export default injectSheet(styles)(Page);


